<template>
  <div style="height:100%;position:relative">
    <mt-header class="search-header" title="搜索">
      <router-link to="/index/explore" slot="left">
        <mt-button icon="back"></mt-button>
      </router-link>
    </mt-header>
    <form action="javascript:void(0);" id="form" ref="form">
      <mt-search ref="search" v-model="key" cancel-text="取消" placeholder="搜索"></mt-search>
    </form>
    <div class="loadmore-wrapper">
      <mt-loadmore :autoFill="false" :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore">
          <mt-cell class="readings" v-for="item in readings" :key="item.id" 
          :title="item.title" :label="getDesc(item.likes,item.author)"></mt-cell>
      </mt-loadmore>
    </div>
  </div>
</template>
<script>
import { READINGS } from "../api/paths";
let { readings } = READINGS;

export default {
  name: "search",
  mounted() {
    let that = this;
    let input = this.$refs.search.$refs.input;
    this.$refs.form.onsubmit = function() {
      that.$http.get(readings).then(res => {
        if (res.data.errcode === 0) {
          that.readings = res.data.data;
          input.blur();
        }
      });
    };
  },
  data() {
    return {
      key: "",
      readings: [],
      allLoaded: false
    };
  },
  methods: {
    loadTop() {
      this.$http.get(readings).then(res => {
        if (res.data.errcode === 0) {
          this.readings = res.data.data;
          this.$refs.loadmore.onTopLoaded();
        }
      });
    },
    loadBottom() {
      this.$http.get(readings).then(res => {
        if (res.data.errcode === 0) {
          this.readings = this.readings.concat(res.data.data);
          this.$refs.loadmore.onBottomLoaded();
        }
      });
    },
    getDesc(likes, author, date) {
      return likes + "人喜欢·" + author;
    }
  }
};
</script>
<style>
.search-header .mint-header-title {
  color: black;
  font-weight: bold;
  font-size: 17px;
}
.search-header .mintui {
  font-size: 17px;
  font-weight: bold;
}
#form .mint-search-list {
  display: none;
}
</style>
<style scoped>
#form .mint-search {
  height: 44px;
}
.loadmore-wrapper {
  position: absolute;
  top: 84px;
  bottom: 0px;
  width: 100%;
  overflow: scroll;
  overflow-x: hidden;
}
.mint-header {
  background: white;
  color: blue;
}
</style>




